<template>
  <div class="login">
    <HeaderView/>
    <div class="login-wrapper">
      <div class="logo">
        <img src="../assets/logo.png" alt="logo">
      </div>
      <Form class="form">
        <FormItem :error="errors.userName">
          <Input
            prefix="ios-contact"
            v-model.trim="user.userName"
            size="large"
            type="text"
            placeholder="用户名"
            style="width: 100%"
            @input="errors.userName=''"
          />
        </FormItem>
        <FormItem :error="errors.userPassword">
          <Input
            prefix="ios-lock-outline"
            v-model.trim="user.userPassword"
            size="large"
            type="password"
            placeholder="密码"
            style="width: 100%"
            @input="errors.userPassword=''"
          />
        </FormItem>
        <FormItem :error="errors.verifiyCode">
          <div class="verifiycode">
            <Poptip trigger="hover" content="看不清楚？换一张">
              <img
                :src="verifiyUrl"
                alt="图形验证码"
                @load="verifiyStatus = true"
                @click="toogleVerify($event)"
              >
            </Poptip>
            <Input
              prefix="ios-code"
              v-model.trim="user.verifiyCode"
              size="large"
              type="text"
              style="width: auto"
              placeholder="验证码"
              @input="errors.verifiyCode=''"
            />
          </div>
        </FormItem>
        <FormItem>
          <Button type="primary" size="large" long @click.prevent="submit">{{btnText}}</Button>
        </FormItem>
      </Form>
    </div>
  </div>
</template>

<script>
import HeaderView from "@/components/HeaderView";
import axios from "axios";
import { mapMutations } from "vuex";
export default {
  components: {
    HeaderView
  },
  data() {
    return {
      user: {
        userName: "",
        userPassword: "",
        verifiyCode: ""
      },
      errors: {},
      verifiyUrl: "http://192.168.2.129:3000/verifiycode",
      verifiyStatus: false,
      btnText: "登录"
    };
  },
  methods: {
    ...mapMutations(["updateUser"]),
    submit() {
      if (!this.user.userName)
        this.$set(this.errors, "userName", "用户名不能为空");
      if (!this.user.userPassword)
        this.$set(this.errors, "userPassword", "密码不能为空");
      if (!this.user.verifiyCode)
        this.$set(this.errors, "verifiyCode", "验证码不能为空");

      let length = Object.keys(this.errors).filter(key => {
        return this.errors[key].length > 0;
      });

      if (length < 1) {
        this.btnText = "登录中......";
        axios
          .post("http://192.168.2.129:3000/api?action=login", this.user)
          .then(({ data }) => {
            if (data.code === 1) {
              this.errors = {};
              this.user.userPassword = "";
              this.user.verifiyCode = "";
              this.updateUser(this.user);
              this.$router.push("/");
            } else if (data.code === 0) {
              this.btnText = "登录";
              this.errors = data.errors;
            }
          })
          .catch(err => {
            console.log(err);
          });
      } else {
        return;
      }
    },
    toogleVerify(event) {
      if (!this.verifiyStatus) return;
      let base = "http://192.168.2.129:3000/verifiycode";
      // console.log(event.timeStamp);
      this.verifiyUrl = base + "?" + event.timeStamp;
    }
  }
};
</script>

<style scoped>
@import url("../assets/css/login.css");
</style>


